// Hooks
import { observer } from 'mobx-react-lite';
import { useStore, useTags } from '../hooks';

// Helpers
import { fetchify, getTagIcon } from '../helpers';

export const Tags = observer(({ tipViewMode }) => {
    const { data, isFetched } = useTags();
    const { tagStore } = useStore();
    const { selectedTagId, setSelectedTagId } = tagStore;

    const tagsJSX = Array.isArray(data) && data.map(({ id, name }) => {
        const TagIcon = getTagIcon(name);

        const handleClick = () => {
            setSelectedTagId(id);
        };

        return (
            <span
                data-active = { selectedTagId === id || tipViewMode === 'all-topics' }
                key = { id }
                className = 'tag'
                onClick = { handleClick }>
                <TagIcon />
                { name }
            </span>
        );
    });

    return (
        <div className = 'tags'>
            { fetchify(isFetched, tagsJSX) }
        </div>
    );
});
